.CreateTodoButton {
  background-color: #2d266d;
  box-shadow: 0px 5px 25px rgba(22, 17, 41, 0.8);
  border: none;
  border-radius: 50%;
  cursor: pointer;
  font-size: 50px;
  position: fixed;
  bottom: 24px;
  right: 24px;
  font-weight: bold;
  color: whitesmoke;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60px;
  width: 60px;

  transform: rotate(0);
  transition: .3s ease;
  z-index: 1;
}

.CreateTodoButton:hover {
  transform: rotate(224deg);
}
