@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@1,300&display=swap);
.TodoCounter {
  color: whitesmoke;
  font-size: 32px;
  text-align: center;
  margin: 0;
  padding: 48px;
  font-family: 'Poppins';
}

.TodoSearch {
  background: whitesmoke;
  border-radius: 12px;
  border: 1px;
  margin: 0 24px;
  height: 40px;
  width: calc(100% - 62px);
  font-size: 20px;
  text-align: center;
  font-family: 'Montserrat';
  font-weight: 400;
  color: #2c2b33;
}

.TodoSearch::-webkit-input-placeholder {
  color: #bbbbbb;
  font-family: 'Montserrat';
  font-weight: 400;
  font-style: italic;
}

.TodoSearch::placeholder {
  color: #bbbbbb;
  font-family: 'Montserrat';
  font-weight: 400;
  font-style: italic;
}

.TodoSearch:focus {
  outline-color: gray;
}

ul {
  margin: 0;
  padding: 0 0 56px 0;
  list-style: none;
}

.TodoItem {
  background-color: rgba(34, 33, 117, 0.40);
  border-radius: 12px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 24px;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
  color: whitesmoke;
}

.TodoItem-p {
  margin: 24px 0 24px 24px;
  width: calc(100% - 120px);
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
}
.TodoItem-p--complete {
  text-decoration: line-through;
}

.Icon {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 48px;
  width: 48px;
  font-size: 24px;
  font-weight: bold;
}

.Icon-check {
  position: absolute;
  left: 12px;
}
.Icon-check--active {
  color: #00ff08;
}

.Icon-delete {
  width: 40px;
  height: 40px;
  position: absolute;
  top: -8px;
  right: 0;
}
.Icon-delete:hover {
  color: red;
}

form {
  width: 90%;
  max-width: 300px;
  background-image: url(/To-Do-App/static/media/fondo.3cfb2a71.png);
  padding: 33px 40px;
  display: flex;
  border-radius: 26px;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-family: "Poppins";
}

label {
  text-align: center;
  font-weight: bold;
  font-size: 20px;
  color: whitesmoke;
  margin-bottom: 26px;
}

textarea {
  background-color: whitesmoke;
  border: 2px solid white;
  border-radius: 20px;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
  color: #2d266d;
  font-size: 20px;
  text-align: center;
  padding: 12px;
  height: 96px;
  width: calc(100% - 10px);
}

textarea::-webkit-input-placeholder {
  color: #bbbbbb;
  font-family: 'Montserrat';
  font-style: italic;
  font-weight: 400;
}

textarea::placeholder {
  color: #bbbbbb;
  font-family: 'Montserrat';
  font-style: italic;
  font-weight: 400;
}

textarea:focus {
  outline-color: #bbbbbb;
}

.TodoForm-buttonContainer {
  margin-top: 14px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.TodoForm-button {
  cursor: pointer;
  display: inline-block;
  font-size: 16px;
  color: wheat;
  font-weight: 400;
  width: 120px;
  height: 30px;
  border-radius: 10px;
  border: none;
  font-family: 'Montserrat';
}

.TodoForm-button--add {
  font-weight: bold;
  background: #2d266d;
  box-shadow: 0px 5px 25px rgba(53, 70, 29, 0.5);
}

.TodoForm-button--cancel {
  font-weight: bold;
  background: transparent;
}

.CreateTodoButton {
  background-color: #2d266d;
  box-shadow: 0px 5px 25px rgba(22, 17, 41, 0.8);
  border: none;
  border-radius: 50%;
  cursor: pointer;
  font-size: 50px;
  position: fixed;
  bottom: 24px;
  right: 24px;
  font-weight: bold;
  color: whitesmoke;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60px;
  width: 60px;

  -webkit-transform: rotate(0);

          transform: rotate(0);
  transition: .3s ease;
  z-index: 1;
}

.CreateTodoButton:hover {
  -webkit-transform: rotate(224deg);
          transform: rotate(224deg);
}

.ModalBackground {
  background: rgba(32,35,41,.8);
  position: fixed;
  top: -10px;
  left: -10px;
  right: -10px;
  bottom: -10px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}

.TodoGif {
    background-image: url(/To-Do-App/static/media/giphy.bfecba79.gif);
    width: 100vw; 
    height: 100vh;
}
body {
  margin: 0;
  padding: 0;
  background: #f2f4f5;
  background-image: url(/To-Do-App/static/media/fondo.3cfb2a71.png);
  color: whitesmoke;
  text-shadow: 2px 4px 8px #000000;
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
  
}

#root {
  margin: 0 24px;
  position: relative;
  min-height: 100vh;
}

